import React, {useState} from "react"
import { AppBar, Avatar, Box, Divider, IconButton, List, ListItem, ListItemIcon, ListItemText, Toolbar, Typography } from "@material-ui/core"
import { Apps, ArrowBack, AssignmentInd, ContactMail, Home, MenuOpen } from "@material-ui/icons"
import CVImg from "../img/cv_img.jpg"
import styled, { css } from 'styled-components'
import { withTheme } from "@material-ui/core/styles";
import MobileRightMenuSlider from "@material-ui/core/Drawer"

const MenuContainer = styled(Box)`
  width: 250px;
  background: #511;
  height: 100%;
`

const AvatarImage = withTheme(styled(Avatar)`
  &&&{
    display: block;
    margin: 0.5rem auto;
    width: ${props => props.theme.spacing(13)}px;
    height: ${props => props.theme.spacing(13)}px;
  }
`);

const baseFontColor = css`
  color: tan;
`

const NavBarListIcon = styled(ListItemIcon)`
  &&&{
    ${baseFontColor}
  }
`
const NavBarListText = styled(ListItemText)`
  ${baseFontColor}
`

const menuItems = [
  {
    icon: <Home/>,
    text: "Home"
  },
  {
    icon: <AssignmentInd/>,
    text: "Resume"
  },
  {
    icon: <Apps/>,
    text: "Portfolio"
  },
  {
    icon: <ContactMail/>,
    text: "Contact"
  }
]

const Navbar = () => {

  let [showMenu, setShowMenu] = useState({
    right: false
  });

  const toggleSlider = (slider, open) => {
    setShowMenu({...showMenu, [slider]: open })
  }

  const sideList = slider => (
    <MenuContainer component="div" onClick={() => toggleSlider("right", false)}>
      <AvatarImage src={CVImg} alt="Alex Gonz치lez"/>
      <Divider/>
      <List>
        {menuItems.map((item,key) => (
          <ListItem button key={key}>
            <NavBarListIcon>{item.icon}</NavBarListIcon>
            <NavBarListText>{item.text}</NavBarListText>
          </ListItem>
        ))}
      </List>
    </MenuContainer>
  );

  return(
    <>
    <Box component="nav">
      <AppBar position="static" style={{background: "#222"}}>
        <Toolbar>
          <IconButton onClick={() => toggleSlider("right", true) }>
            <MenuOpen style={{color: "#b11010"}}/>
          </IconButton>
          <Typography variant="h5"  style={{color: "tan"}}>Alex Gonz치lez</Typography>
        </Toolbar>
        <MobileRightMenuSlider 
        anchor="right" 
        open={showMenu.right}
        onClose={() => toggleSlider("right", false)}
        >
          {sideList("right")}
        </MobileRightMenuSlider>
      </AppBar>
    </Box>
    </>
  )
}

export default Navbar;