import { FunctionComponent, useState } from 'react';
import ReactTerminalStateless from 'react-terminal-component';
import * as javascriptTerminal from 'javascript-terminal';

type TerminalProps = {
  type: string
}

export const Terminal: FunctionComponent<TerminalProps> = ({ type }) => { 
  const [input, setInput] = useState('');
  const [state, setState] = useState(javascriptTerminal.EmulatorState.create({
    'fs': javascriptTerminal.FileSystem.create({
      '/home': { },
      '/home/README': {content: 'This is a text file'},
      '/home/nested/directory': {},
      '/home/nested/directory/file': {content: 'End of nested directory!'}
    })
  }));

  return(
    <>
     <ReactTerminalStateless
        emulatorState={state}
        inputStr={input}
        onInputChange={(inputStr) => setInput(inputStr)}
        onStateChange={(emulatorState) => setState(emulatorState)}
        promptSymbol
     />
    </>
  )
}