import React, {useState} from "react"
import { AppBar, Avatar, Box, Divider, Grid, IconButton, List, ListItem, ListItemIcon, ListItemText, Toolbar, Typography } from "@material-ui/core"
import { Apps, ArrowBack, AssignmentInd, ContactMail, Home, MenuOpen } from "@material-ui/icons"
import CVImg from "../img/cv_img.jpg"
import styled, { css } from 'styled-components'
import { makeStyles, withTheme } from "@material-ui/core/styles";
import MobileRightMenuSlider from "@material-ui/core/Drawer"
import Typed from "react-typed"


const menuItems = [
  {
    icon: <Home/>,
    text: "Home"
  },
  {
    icon: <AssignmentInd/>,
    text: "Resume"
  },
  {
    icon: <Apps/>,
    text: "Portfolio"
  },
  {
    icon: <ContactMail/>,
    text: "Contact"
  }
]

const AvatarImg = withTheme(styled(Avatar)`
    width: ${props => props.theme.spacing(15)}px;
    height: ${props => props.theme.spacing(15)}px;
    margin: ${props => props.theme.spacing(15)};
`)

const MainContainer = styled(Box)`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  text-align: center;
  z-index: 1
;

`

const Title = styled(Typography)`
  color: tan;
`

const Subtitle = styled(Typography)`
  color: tomato;
  margin-bottom: 3rem;
`

const Header = () => {
 
  return(
    <>
    <MainContainer>
      <Grid container justify={"center"}>
        <AvatarImg src={CVImg} alt="Alex Gonz치lez"/>
      </Grid>
      <Title variant="h4">
        <Typed 
        strings={["Alex Gonz치lez"]}
        typeSpeed={40}
        loop= {false}
        />
      </Title>
      <br/>
      <Subtitle variant="h5">
        <Typed 
        strings={["C# | .NET  | NodeJS", "React | Redux | TypeScript", "Docker | K8S | RabbitMQ", "GIT | Jenkins | Redis", "Full Stack Developer" ]}
        typeSpeed={40}
        backSpeed={50}       
        />
      </Subtitle>
    </MainContainer>
    </>
  )
}

export default Header;